// Get the stored ngrok URL from localStorage if available
const storedNgrokUrl = localStorage.getItem('ngrokUrl');
const isDevelopment = window.location.hostname === 'localhost';

const config = {
  // Priority:
  // 1. Use ngrok URL from localStorage if available
  // 2. Use localhost for local development
  // 3. Use deployed API URL for production
  API_URL: storedNgrokUrl || 
    (isDevelopment ? 'http://localhost:5000' : 'https://your-deployed-api-url.com')
};

// Log configuration info
console.log('Environment:', isDevelopment ? 'Development' : 'Production');
console.log('Using API URL:', config.API_URL);
console.log('Using ngrok:', !!storedNgrokUrl);

// Helper function to set ngrok URL
export const setNgrokUrl = (url) => {
  if (url && url.includes('ngrok')) {
    localStorage.setItem('ngrokUrl', '  https://619d-2600-4040-441b-5e00-8534-daac-9f21-baa.ngrok-free.app');
    console.log('Ngrok URL updated. Please refresh the page.');
    return true;
  }
  return false;
};

// Helper to clear ngrok URL
export const clearNgrokUrl = () => {
  localStorage.removeItem('ngrokUrl');
  console.log('Ngrok URL cleared. Using default API URL after refresh.');
};

export default config;
